.ingredient {
    --jed: 1vh;
    position: absolute;
    width: calc(18 * var(--jed));
    height: calc(18 * var(--jed));
    transition: transform .1s;
    outline: none;
    border-radius: 50%;

    @keyframes selector {
        0% {
            transform: rotate(0deg); }
        25% {
            transform: rotate(25deg); }
        50% {
            transform: rotate(0deg); }
        75% {
            transform: rotate(-25deg); }
        100% {
            transform: rotate(0deg); } }

    @media (orientation: portrait) {
        --jed: 1.2vw;
        transition: transform 0s; }

    &--chilli_red, &--chilli_yellow, &--chilli_green, &--feta {
        width: calc(6 * var(--jed));
        height: calc(6 * var(--jed)); }

    &--ham, &--prosciutto {
        width: calc(28 * var(--jed));
        height: calc(12 * var(--jed)); }

    &--salami {
        width: calc(14 * var(--jed));
        height: calc(14 * var(--jed)); }

    &--olive_green, &--olive_black {
        width: calc(6 * var(--jed));
        height: calc(10 * var(--jed)); }

    &--cocktail_tomato, &--shrimp {
        width: calc(12 * var(--jed));
        height: calc(12 * var(--jed)); }

    &--pineapple, &--mussel {
        width: calc(8 * var(--jed));
        height: calc(8 * var(--jed)); }

    &--shrimp_shell {
        width: calc(24 * var(--jed));
        height: calc(14 * var(--jed)); }

    &--shroom_dark, &--shroom_light {
        width: calc(16 * var(--jed));
        height: calc(12 * var(--jed)); }

    &--camembert, &--mussel_opened, &--mussel_closed, &--octopus {
        width: calc(20 * var(--jed));
        height: calc(8 * var(--jed)); }

    &--mussel_opened, &--mussel_closed {
        width: calc(22 * var(--jed));
        height: calc(14 * var(--jed)); }

    &--camembert_half {
        width: calc(12 * var(--jed));
        height: calc(8 * var(--jed)); }

    &--rokpol_blue, &--rokpol_gold {
        width: calc(18 * var(--jed));
        height: calc(14 * var(--jed)); }

    &--rucola {
        width: calc(28 * var(--jed));
        height: calc(16 * var(--jed)); }

    &--basil {
        width: calc(16 * var(--jed));
        height: calc(8 * var(--jed)); }

    &:focus svg {
        animation: selector 2s 1; } }
