html,
body {
  height: 100%;
  width: 100%;
  background-color: #3dbacc;
  overflow: hidden;
  font-family: 'Amatic SC', cursive;
  font-weight: 700;
  box-sizing: border-box;
}

#root {
  height: 100%;
  width: 100%;
}

svg {
  overflow: visible;
  width: 100%;
  height: 100%;
}